import { Component, OnInit } from '@angular/core';

import { CompanyService } from '../../services/company.service';
import { Config } from './../../config/config';
import { CanActivate, Router } from '@angular/router';
import * as socketIo from 'socket.io-client';
@Component({
  selector: 'app-company-top-bar',
  templateUrl: './company-top-bar.component.html',
  styleUrls: ['./company-top-bar.component.css']
})
export class CompanyTopBarComponent implements OnInit {
showHeading :Boolean = false;
noData :Boolean =false;
dataEmail :any;
count :any;
dataArray =[];
userData ={
  userEmail : '',
  userId : ''
}
company_name : any;
userId: any;
viewNotification :Boolean = false;
private socket:any;
  constructor(private companyService: CompanyService, private config: Config, private routes: Router) { this.socket = socketIo(config.siteUrl); }

  ngOnInit() {
    this.logChecking();
    this.loadData();
    this.socket.on('acceptuser', (data) => {
      this.loadData();
    });
    this.socket.on('rejectuser', (data) => {
      this.loadData();
    });
    this.socket.on('expiredcompany', (data) => {
      // this.logChecking();
      this.tokenUpdate();
    });
  }

  logChecking() {
    // ---------------------------------Start-------------------------------------------
    // Function      : get logged company details
    // Params        : 
    // Returns       : company details
    // Author        : Rinsha
    // Date          : 16-1-2018
    // Last Modified : 16-1-2018, Rinsha
    // Desc          :
    this.companyService.getLoggedUSerDetails().subscribe(info => {
      if (info == null || info == '') {
        this.routes.navigate(['/clogin']);
      }
      this.company_name = info.organization;
      // console.log(this.company_name);
      if (info.role == "admin") {
        this.routes.navigate(['/admin-dashboard']);
      }
      if (info.role == "user") {
        if (info.delete_status == true || info.block_status == true) {
          this.routes.navigate(['/404']);
        }
        this.routes.navigate(['/survey', info.surveyId]);
      }
      if (info.role == "company") {
        if (info.delete_status == true || info.block_status == true || info.cmp_status == "Not Verified") {
          this.routes.navigate(['/clogin']);
        }
        if (info.cmp_status == "Expired") {
          this.routes.navigate(['/expired']);
        }
        if (info.is_profile_completed == false) {
          this.routes.navigate(['/additnInfo', info._id]);
        }
      }
    });
    // ---------------------------------End-------------------------------------------
  }
  
  loadData(){
      this.companyService.getAcceptedNotification().subscribe(data => {
       // console.log(data);
          this.count = data.length;
          if(data.length){
            this.showHeading =true
            this.noData =false
          }
          if(!data.length){
            this.noData =true
            this.showHeading =false
          }
          // console.log(this.count);
          this.dataArray= [];
          data.forEach(element => {
              this.dataArray.push(element);
         });
     });
   }
  notifViewed(email,id){
   // console.log(id);
    // console.log(email);
    this.userData.userEmail = email;
    this.userData.userId = id
    // console.log(this.userData);
    // console.log(this.dataArray)
    this.companyService.updateNotifViewed(this.userData).subscribe(data => {
      if (data.success) {
        this.loadData();
        // window.location.reload();
      }
      //  console.log(data)
    });

  }

  // ---------------------------------Start-------------------------------------------
  // Function      : Logout
  // Params        : 
  // Returns       : 
  // Author        : Rinsha
  // Date          : 03-1-2018
  // Last Modified : 03-1-2018, Rinsha
  // Desc          : 
  logout() {
    this.companyService.logout();
    this.routes.navigate(['/home']);
    return false;
  }
  // -----------------------------------End------------------------------------------

  upgrade() {
    this.routes.navigate(['/upgrade']);
  }

  tokenUpdate(){
    this.companyService.tokenUpdate().subscribe(info1 => {
      this.companyService.storeUserData(info1.token, info1.company);
    });
    this.logChecking();
  }
}



